<style type="text/css" scoped>
	.header_w{
		width: 100%;
		height: .9rem;
		position: fixed;
		z-index: 10;
	}
	.web_logo{
		width: 1.59rem;
		position: absolute;
		left: 2.2%;
		bottom: .14rem;
		cursor: pointer;
	}
	.right_blobk{
		position: absolute;
		right: 0;
		bottom: 0;
	}
	.cuput{
		float: right;
		width: 1.56rem;
		height: .58rem;
		position: relative;
		margin-right: .16rem;
		cursor: pointer;
		-webkit-transition: color .3s cubic-bezier(.4,0,0,1);
		transition: color .3s cubic-bezier(.4,0,0,1);
	}
	.cuput_btn{
		display: block;
		width: .58rem;
		height: .57rem;
		background: url(../assets/header/cuput.png) no-repeat top center/100% auto;
		position: absolute;
		right: 0;
		top: 0;
		z-index: 2;

	}
	.cuput_txt{
		-webkit-transform: translateX(.8rem);
		-ms-transform: translateX(.8rem);
		transform: translateX(.8rem);
		-webkit-transition: opacity .15s cubic-bezier(.4,0,0,1),-webkit-transform .3s cubic-bezier(.4,0,0,1);
		transition: opacity .15s cubic-bezier(.4,0,0,1),-webkit-transform .3s cubic-bezier(.4,0,0,1);
		transition: transform .3s cubic-bezier(.4,0,0,1),opacity .15s cubic-bezier(.4,0,0,1);
		transition: transform .3s cubic-bezier(.4,0,0,1),opacity .15s cubic-bezier(.4,0,0,1),-webkit-transform .3s cubic-bezier(.4,0,0,1);
		opacity: 0;
	}
	.cuput_txt{
		display: inline-block;
		position: absolute;
		z-index: 1;
		font-size: .18rem;
		width: 100%;
		top: 28%;
		right: 15%;
		color: #8a8a8a;
	}
	.cuput:hover .cuput_txt {
		-webkit-transform: translateX(0);
		-ms-transform: translateX(0);
		transform: translateX(0);
		opacity: 1;
		-webkit-transition-delay: .05s;
		transition-delay: .05s;
	}
	.cuput::before {
		display: inline-block;
		height: 100%;
		content: "";
		vertical-align: middle;
	}
	.cuput::after {
		opacity: 0;
		-webkit-transform: scale(.5);
		-ms-transform: scale(.5);
		transform: scale(.5);
	}
	.cuput::after {
		content: "";
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		border-radius: .76rem;
		background-color: #dcdcdc;
		-webkit-transform: scale(1);
		-ms-transform: scale(1);
		transform: scale(1);
		-webkit-transition: opacity .15s cubic-bezier(.4,0,0,1),background-color .3s cubic-bezier(.4,0,0,1),-webkit-transform .3s cubic-bezier(.4,0,0,1);
		transition: opacity .15s cubic-bezier(.4,0,0,1),background-color .3s cubic-bezier(.4,0,0,1),-webkit-transform .3s cubic-bezier(.4,0,0,1);
		transition: transform .3s cubic-bezier(.4,0,0,1),opacity .15s cubic-bezier(.4,0,0,1),background-color .3s cubic-bezier(.4,0,0,1);
		transition: transform .3s cubic-bezier(.4,0,0,1),opacity .15s cubic-bezier(.4,0,0,1),background-color .3s cubic-bezier(.4,0,0,1),-webkit-transform .3s cubic-bezier(.4,0,0,1);
	}
	.cuput:hover::after {
		opacity: 1;
		-webkit-transform: scale(1.2);
		-ms-transform: scale(1.2);
		transform: scale(1.2);
	}
	.user{
		float: right;
		width: 1.76rem;
		height: .58rem;
		display: flex;
		align-items: center;
		position: relative;
		z-index: 2;
		cursor: pointer;
		background: url(../assets/header/user_block.png) no-repeat top center/ 100% auto;
	}
	.avatar{
		width: .44rem;
		height: .44rem;
		border-radius: 50%;
		overflow: hidden;
		margin: 0 .12rem 0 .1rem;
		box-sizing: border-box;
		border:.02rem solid rgba(80,80,80,.3);
		background-color: #bf9c9a;
		position: relative;
	}
	.avatar img{
		position: absolute;
		left: -8%;
		width: 100%;
		height: auto;
	}
	.nickName{
		font-size: .16rem;
		color: #ffffff;
		width: 1rem;
		text-align: left;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	.login_btn{
		font-size: .16rem;
		color: #ffffff;
		margin-left: .5rem;
	}
	.fadeIn{
		animation-duration: 2.5s;
		animation-delay: .3s;
	}
	
	.loadingFont{
		opacity: 0;
		font-size: 0;
		font-family: "pfht", "japanhymc", "yrdzsl-Bold", "fzchft", "fzmhft", "fzytft", "FZHLFW", "bulmer_bt", "FZHLFW","fzxlft","hkktlj","fzfmjt",'fzzch','fzmshljt','ygytkjt','jhrhz','fzwbft';
	}
</style>

<template>
	<transition appear appear-active-class="animated fadeIn">
		<div class="header_w">
			<div class="loadingFont">字体加载</div>
			<img src="../assets/header/logo.png" class="web_logo" @click="backHome">
			<div class="right_blobk">
				<div class="user">
					<div class="avatar" v-if="isLogin" @click="go_personal_func">
						<img src="../assets/header/head.png" height="74" width="52">
					</div>
					<div class="nickName" v-if="isLogin">{{currentUser}}</div>
					<span class="login_btn" @click="loginFunc" v-if="!isLogin">登录</span>
				</div>
				<transition  enter-active-class="animated zoomInLeft" leave-active-class="animated zoomOutRight">
					<div class="cuput" @click="toCutout" v-if="isCutout && isLogin">
						<span class="cuput_btn"></span>
						<span class="cuput_txt">智能抠图</span>
					</div>
				</transition>
			</div>
		</div>
	</transition>
</template>

<script type="text/javascript">
	import {mapState, mapGetters, mapMutations} from 'vuex' //引入state，getters，mutations
	export default {
		name: 'block-w',
		props: {
			// isCutout: Boolean
		},
		data: function(){
			return {

			}
		},
		computed: {
			...mapState(['currentUser', 'isLogin']),
			isCutout: function() {
				return this.$route.path == '/cutout' ? false : true
			}
		},
		methods: {
			loginFunc: function() {
				this.openLogin()
			},
			toCutout: function() {
				this.$router.push('/cutout')
			},
			backHome: function() {
				this.$router.push('cover')
			},
			go_personal_func: function() {
				if(this.$route.path.indexOf('/personal') != -1){
					return 
				}else {
					this.$router.push('/personal/')
				}
			}


		}
	}

</script>